html, body, div, span, a, b, u, i,
h1, h2, h3, h4, h5, h6, p, q, blockquote,
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section
ol, ul, li, button, img,
form, input, textarea, label {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 62.5%;
  font: inherit;
  color: inherit;
  vertical-align: baseline;
  background: transparent;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

:focus {
  outline: 0;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

a,
a:hover {
  color: inherit;
  text-decoration: none;
}

a:active,
a:hover {
  outline: 0
}

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}

blockquote, q {
	quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
	content: "";
	content: none;
}

input,
label,
select,
button,
textarea {
  display: inline-block;
  vertical-align: baseline;
  white-space: normal;
  line-height: 1;
}

textarea {
  vertical-align: top;
  overflow: auto;
  resize: vertical;
  min-height: 2rem;
}

input[type=checkbox],
input[type=radio] {
  margin: 0.5rem;
  width: 1rem;
  height: 1rem;
  padding: 0;
}

input[type=search] {
  -webkit-appearance: textfield;
}

input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  -webkit-appearance: none;
  -moz-appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

button,
html input[type=button],
input[type=reset],
input[type=submit] {
  -webkit-appearance: button;
  cursor: pointer;
}

button[disabled],
html input[disabled] {
  cursor: default;
}

button::-moz-focus-inner,
input::-moz-focus-inner {
    border: 0;
    padding: 0;
}
body {
  background: #dcf6ff;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  min-width: 800px;
  overflow: overlay;
}

.app {
  max-width: 1300px;
  margin: auto;
  padding: 20px 0;
}

.header-container {
  display: flex;
  justify-content: center;
  height: auto;
  vertical-align: middle;
  align-items: center;
  align-self: center;
  align-content: center;
  padding-bottom: 20px;
  border-bottom: 1px solid #808080;
}

.header-container > div {
  width: 50%;
  height: 100%;
  align-content: center;
  align-self: center;
  vertical-align: middle;
}

.title {
  text-align: center;
}

.title h1 {
  font-weight: bold;
  font-size: 60px;
  padding: 10px
}

.title h2 {
  font-size: 30px;
}

.search-bar {
  border: 1px solid #c0c0c0;
  border-radius: 10px;
  font-size: 20px;
  padding: 10px;
  width: 80%;
  background: #ffffff;
}

.movie-index {
  text-align: center;
}

.movie-index-msg {
  padding-top: 100px;
  font-size: 25px;
}

.movie-index-results {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
}

.pagination {
  display: flex;
  justify-content: center;
}

.pagination li {
  text-align: center;
  margin: 3px;
  min-width: 30px;
  padding: 5px;
  border: 1px solid #d3d3d3;
  background: white;
  border-radius: 3px;
  cursor: pointer;
}

.pagination li:hover {
  background: #f1f1f1;
}

.pagination .active {
  background: #b6d2fc;
  pointer-events: none;
}

.film-icon {
  display: block;
  opacity: .3;
  margin: 200px auto 0 auto;
  height: 100px;
  width: 100px;
}

.welcome {
  padding: 15px;
  font-size: 18px;
  font-weight: bold;
  opacity: .3;
  color: #2870E8;
}

.movie-index-item {
  display: flex;
  width: 80%;
  margin: 20px auto;
}

.movie-poster {
  padding: 10px;
}

.movie-poster img {
  width: 185px;
  height: 278px;
  border: 1px solid rgba(207, 207, 207, 0.5);
  background: #ffffff;
}

.movie-details {
  text-align: left;
  width: auto;
  flex-grow: 1;
  padding: 10px;
}

.movie-details > * {
  padding-bottom: 10px;
}

.movie-title {
  font-size: 35px;
  font-weight: bold;
  text-align: left;
}

.movie-year {
  font-size: 25px;
  text-align: left;
}

.movie-rating {
  display: inline-block;
}

.movie-rating * {
  vertical-align: middle;
}

.movie-overview {
  text-align: justify;
}

